import React from "react";
import { Link } from "gatsby"
import { NavbarWrapper } from "./styles/NavbarStyles";
import styled from "styled-components";

const LogoTop = styled.div`
  display: block;
  width: 220px;
  height: 40px;
  :hover {
      opacity: 0.8;
    }
  @media only screen and (min-width: 624px) {
    margin-top: -1px;
  }

`;

const Contact_Btn = styled.a`
  display:block;
  position: relative;
  right: 0;
  letter-spacing: .04em;
  line-height:6.2vh;
  padding:0 0,3em;
  color:#fff !important;
  background-color:#D0A3B6;
  cursor:pointer;
  :hover {
      opacity: 0.8;
    }
  @media only screen and (min-width: 624px) {
    margin-top: -1px;
    }
  }

`;

function Navbar({ open, handleClick }){
  return (
    <NavbarWrapper open={open}>
<Link onClick={handleClick} to="/"><LogoTop onClick={handleClick} to="/"><img src={require('../images/gorettimatrona_logo.svg')} alt="Goretti Martel Matrona Las Palmas Logo"/></LogoTop></Link>
<Link onClick={handleClick} className="menu__item" to="/embarazo/"><span className="menu__item-name">Embarazo</span></Link>
<Link onClick={handleClick} className="menu__item" to="/cursos/"><span className="menu__item-name">Cursos</span></Link>
<Link onClick={handleClick} className="menu__item" to="/postparto/"><span className="menu__item-name">Postparto</span></Link>
<Link onClick={handleClick} className="menu__item" to="/#servicios_online"><span className="menu__item-name">Lactancia</span></Link>
<Link onClick={handleClick} className="menu__item" to="/#contactFormText"><span className="menu__item-name">Contacto</span></Link>
<Contact_Btn onClick={handleClick} className="project__item-btn gradient_effect" href="/#contactFormText">Pedir cita</Contact_Btn>
    </NavbarWrapper>
  );
}

export default Navbar;
